<template>
    <section>
        <v-flex v-if="is_loading" pa-2>
            <v-alert type="info">Đang nạp dữ liệu...</v-alert>
        </v-flex>

        <v-flex v-else-if="error" pa-2>
            <v-alert type="error">{{ error }}</v-alert>
        </v-flex>

        <v-flex v-else-if="wiki_data === null" pa-2>
            <v-alert type="info">
                Hãy chọn tài liệu cần xem, rê chuột về bên trái để mở danh sách
            </v-alert>
        </v-flex>

        <v-flex v-else pa-2>
            <h1 class="display-1 font-weight-bold mt-3 mb-3 text-center">
                {{ wiki_data.metadata.title }}
            </h1>

            <ContentList :items="wiki_data.contents"></ContentList>

            <div
                ref="markdown"
                class="elevation-1 pa-2"
                v-html="wiki_data.markdown"
            ></div>
        </v-flex>
    </section>
</template>

<script>
import Marked from 'marked';
import ContentList from '../components/content_list';

export default {
    name: 'wiki_detail',

    components: {
        ContentList
    },

    props: {
        wiki: { type: Object }
    },

    data() {
        return {
            is_loading: false,
            error: null,
            wiki_data: null
        };
    },

    methods: {
        async loadData() {
            this.is_loading = true;
            this.error = null;

            let wiki_content;
            try {
                wiki_content = (
                    await this.axios({
                        url: '/wiki/' + this.wiki.key + '.md'
                    })
                ).data;
            } catch (e) {
                if (e.response && e.response.status === 405)
                    this.error = 'Không tìm thấy tài liệu ';
                else if (e.mesasge) this.error = e.mesasge;
                else
                    this.error =
                        'Có lỗi khi nạp tài liệu "' + this.wiki.key + '"';
            }

            this.is_loading = false;
            if (this.error) return;

            this.wiki_data = {
                metadata: this.wiki,
                markdown: Marked(wiki_content, {})
            };

            this.wiki_data.contents = [
                {
                    label: 'Tác giả',
                    value: this.wiki_data.metadata.author
                }
            ];
        }
    },

    watch: {
        async wiki() {
            await this.loadData();
        }
    }
};
</script>

<style lang="scss" scoped></style>
