<template>
    <v-row justify="center">
        <v-col cols="11">
            <v-row no-gutters>
                <v-col cols="2" class="text-right">
                    <v-card
                        v-for="item in items"
                        :key="item.label"
                        class="pa-2"
                        outlined
                        tile
                    >
                        <v-tooltip
                            right
                            color="rgba(0, 0, 0, 1)"
                            :disabled="!item.tooltip"
                        >
                            <template #activator="{ on }">
                                <b v-on="on">{{ item.label }}</b>
                            </template>
                            <span>{{ item.tooltip }}</span>
                        </v-tooltip>
                    </v-card>
                </v-col>
                <v-col cols="10" class="text-left">
                    <v-card
                        v-for="item in items"
                        :key="item.label"
                        class="pa-2"
                        style="height: 42px"
                        outlined
                        tile
                    >
                        <template v-if="!item.is_list">
                            <ContentDisplay
                                :value="item.value"
                                :type="item.type"
                                :chip="item.chip"
                                :link="getLink({ link: item.link, item })"
                                :link-href="item.link_href"
                                :link-target="item.link_target"
                                :link-render="item.link_render"
                                :text-render="item.text_render"
                                :datetime-duration="true"
                            ></ContentDisplay>
                        </template>

                        <template v-else>
                            <ContentDisplay
                                v-for="child of item.value"
                                :key="
                                    item.child_key
                                        ? child[item.child_key]
                                        : child
                                "
                                :value="child"
                                :type="item.type"
                                :chip="item.chip"
                                :link="
                                    getLink({ link: item.link, item: child })
                                "
                                :link-href="item.link_href"
                                :link-target="item.link_target"
                                :link-render="item.link_render"
                                :text-render="item.text_render"
                                :datetime-duration="true"
                            ></ContentDisplay>
                        </template>
                    </v-card>
                </v-col>
            </v-row>
        </v-col>
    </v-row>
</template>

<script>
import ContentDisplay from './content_display';

export default {
    name: 'content_list',

    components: {
        ContentDisplay
    },

    props: {
        items: {
            type: Array,
            required: true
        }
    },

    methods: {
        getLink({ link, item }) {
            if (!link) return;
            if (typeof link === 'string') return link;
            if (typeof link === 'function') return link({ item });
        }
    }
};
</script>

<style lang="scss" scoped></style>
