<template>
    <v-layout wrap>
        <v-flex v-if="this.wikis.length === 0" x12 pa-2>
            <v-alert type="warning"
                >Không có tập tài liệu nào để hiển thị</v-alert
            >
        </v-flex>

        <template v-else>
            <WikiNavBar :wikis="wikis" @change="onSelectWiki"></WikiNavBar>
            <v-flex>
                <WikiDetail :wiki="selected_wiki"></WikiDetail>
            </v-flex>
        </template>
    </v-layout>
</template>

<script>
import WikiNavBar from '../components/wiki_nav_bar';
import WikiDetail from '../components/wiki_detail';

export default {
    name: 'wiki',

    components: {
        WikiNavBar,
        WikiDetail
    },

    data() {
        return {
            wikis: this.$store.state.document.wikis,
            selected_wiki: null
        };
    },

    methods: {
        onSelectWiki(wiki) {
            this.selected_wiki = wiki;
        }
    }
};
</script>

<style lang="scss">
.app-container {
    padding: 0;
}
</style>
