<template>
    <v-layout wrap>
        <v-flex xs12>
            <v-card class="mb-5 pa-2">
                <SearchBox
                    :source_data="modules"
                    :criteries="criteries"
                    @change="onChange"
                ></SearchBox>
            </v-card>
        </v-flex>

        <v-flex xs12>
            <v-tabs grow>
                <v-tab href="#tab-list">Danh sách module</v-tab>
                <v-tab href="#tab-diagram">Sơ đồ module</v-tab>

                <v-tab-item value="tab-list">
                    <v-card>
                        <DataTableModule
                            :modules="filter_modules"
                        ></DataTableModule>
                    </v-card>
                </v-tab-item>

                <v-tab-item value="tab-diagram">
                    <v-card>
                        <Diagram :data="diagram_data"></Diagram>
                    </v-card>
                </v-tab-item>
            </v-tabs>
        </v-flex>
    </v-layout>
</template>

<script>
import SearchBox from '../components/search_box';
import DataTableModule from '../components/data_table_module';
import Diagram from '../components/diagram';

export default {
    name: 'module',

    components: {
        SearchBox,
        DataTableModule,
        Diagram
    },

    data() {
        return {
            modules: this.$store.state.document.modules,
            filter_modules: this.computeModule(
                this.$store.state.document.modules
            ),

            criteries: [
                {
                    default: true,
                    key: 'name',
                    name: 'Tên',
                    description: 'Tìm kiếm theo tên gọi của module'
                },
                {
                    key: 'desc',
                    field: 'description',
                    name: 'Mô tả',
                    description: 'Tìm kiếm theo mô tả của module'
                },
                {
                    key: 'ver',
                    field: 'version',
                    name: 'Phiên bản',
                    description: 'Tìm kiếm theo phiên bản của module'
                },
                {
                    key: 'author',
                    name: 'Tác giả',
                    description: 'Tìm kiếm theo người phụ trách của module'
                }
            ]
        };
    },

    computed: {
        diagram_data() {
            let result = {
                nodes: [],
                edges: []
            };

            // load nodes
            for (let modu of this.filter_modules) {
                result.nodes.push({
                    key: modu.name,
                    name: modu.name,
                    type: 'module'
                });
            }

            // load edge
            for (let modu of this.filter_modules) {
                for (let dependency of modu.module_dependencies) {
                    let edge = result.edges.find(
                        x => x.from === modu.name && x.to === dependency.module
                    );
                    if (edge) continue;

                    result.edges.push({
                        from: modu.name,
                        to: dependency.module
                    });

                    if (!result.nodes.find(x => x.key === dependency.module)) {
                        result.nodes.push({
                            key: dependency.module,
                            name: dependency.module,
                            type: 'module',
                            style: 'stroke-dasharray: 5, 5;'
                        });
                    }
                }
            }

            return result;
        }
    },

    methods: {
        computeModule(items) {
            for (let item of items) {
                item.api_count = item.apis.length;
                item.biz_count = item.bizs.length;
                item.model_count = item.models.length;

                item.dependency_count = [];
                for (let dependency of item.module_dependencies) {
                    if (item.dependency_count.includes(dependency.module))
                        continue;
                    item.dependency_count.push(dependency.module);
                }
                item.dependency_count = item.dependency_count.length;

                item.dependent_count = 0;
                for (let modu of this.$store.state.document.modules) {
                    if (modu.name === item.name) continue;
                    if (
                        modu.module_dependencies.some(
                            x => x.module === item.name
                        )
                    ) {
                        item.dependent_count += 1;
                        continue;
                    }
                }
            }
            return items;
        },

        onChange(items) {
            this.filter_modules = this.computeModule(items);
        }
    }
};
</script>

<style lang="scss" scoped></style>
