import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from './views/home.vue';
import Module from './views/module.vue';
import Api from './views/api.vue';
import Model from './views/model.vue';
import Wiki from './views/wiki.vue';
import ModuleDetail from './views/module_detail.vue';
import ApiDetail from './views/api_detail.vue';
import ModelDetail from './views/model_detail.vue';
import PageNotFound from './views/_error_404.vue';

Vue.use(VueRouter);

export default new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'home',
            title: 'Trang chủ',
            component: Home
        },
        {
            path: '/module',
            name: 'module',
            title: 'Module',
            component: Module
        },
        {
            path: '/api',
            name: 'api',
            title: 'Api',
            component: Api
        },
        {
            path: '/model',
            name: 'model',
            title: 'Model',
            component: Model
        },
        {
            path: '/wiki',
            name: 'wiki',
            title: 'Wiki',
            component: Wiki
        },
        {
            path: '/swagger',
            name: 'swagger',
            title: 'Swagger',
            target: '_blank'
        },
        {
            path: '/module/detail',
            name: 'module_detal',
            navbar_show: false,
            component: ModuleDetail
        },
        {
            path: '/api/detail',
            name: 'api_detal',
            navbar_show: false,
            component: ApiDetail
        },
        {
            path: '/model/detail',
            name: 'model_detal',
            navbar_show: false,
            component: ModelDetail
        },
        {
            path: '*',
            name: 'error_404',
            navbar_show: false,
            component: PageNotFound
        }
    ]
});
