<template>
    <v-layout wrap>
        <v-flex xs12>
            <v-card class="mb-5 pa-2">
                <search-box
                    :source_data="models"
                    :criteries="criteries"
                    @change="onChange"
                ></search-box>
            </v-card>
        </v-flex>

        <v-flex xs12>
            <v-tabs grow>
                <v-tab href="#tab-list">Danh sách model</v-tab>
                <v-tab href="#tab-diagram">Sơ đồ model</v-tab>

                <v-tab-item value="tab-list">
                    <v-card>
                        <data-table-model
                            :models="filter_models"
                        ></data-table-model>
                    </v-card>
                </v-tab-item>

                <v-tab-item value="tab-diagram">
                    <v-card>
                        <Diagram
                            :data="diagram_data"
                            :show-edge-text="true"
                        ></Diagram>
                    </v-card>
                </v-tab-item>
            </v-tabs>
        </v-flex>
    </v-layout>
</template>

<script>
import SearchBox from '../components/search_box';
import DataTableModel from '../components/data_table_model';
import Diagram from '../components/diagram';

export default {
    name: 'model',

    components: {
        SearchBox,
        DataTableModel,
        Diagram
    },

    data() {
        return {
            models: this.$store.state.document.models,
            filter_models: this.$store.state.document.models,

            criteries: [
                {
                    default: true,
                    key: 'name',
                    name: 'Tên',
                    description: 'Tìm kiếm theo tên gọi của model'
                },
                {
                    key: 'desc',
                    field: 'description',
                    name: 'Mô tả',
                    description: 'Tìm kiếm theo mô tả của model'
                },
                {
                    key: 'module',
                    name: 'Module',
                    description: 'Tìm kiếm theo module khai báo của model',
                    filter: ({ item, keyword }) => {
                        for (let modu of item.modules) {
                            if (modu.toLocaleLowerCase().includes(keyword))
                                return true;
                        }
                        return false;
                    }
                }
            ]
        };
    },

    computed: {
        diagram_data() {
            let result = {
                nodes: [],
                edges: []
            };

            // load nodes
            for (let model of this.filter_models) {
                result.nodes.push({
                    key: model.name,
                    name: model.name,
                    type: 'model'
                });
            }

            // load edge
            for (let model of this.filter_models) {
                for (let field_key in model.field_link) {
                    if (!model.field_link.hasOwnProperty(field_key)) continue;
                    let link = model.field_link[field_key];

                    result.edges.push({
                        from: model.name,
                        to: link.model,
                        text: field_key
                    });

                    if (!result.nodes.find(x => x.key === link.model)) {
                        result.nodes.push({
                            key: link.model,
                            name: link.model,
                            type: 'model',
                            style: 'stroke-dasharray: 5, 5;'
                        });
                    }
                }
            }

            return result;
        }
    },

    methods: {
        onChange(items) {
            this.filter_models = items;
        }
    }
};
</script>

