<template>
    <DataTable :headers="headers" :items="apis"></DataTable>
</template>

<script>
import DataTable from './data_table';

export default {
    name: 'data_table_api',

    components: {
        DataTable
    },

    props: {
        apis: {
            type: Array,
            required: true
        }
    },

    data() {
        return {
            headers: [
                {
                    text: 'End-Point',
                    value: 'endpoint',
                    type: 'link',
                    link: ({ item }) => '/api/detail?key=' + item.key
                },
                {
                    text: 'Phương thức',
                    value: 'method',
                    type: 'http_method'
                },
                {
                    text: 'Phiên bản',
                    value: 'version'
                },
                {
                    text: 'Mô tả',
                    value: 'summary'
                },
                {
                    text: 'Phân loại',
                    value: 'tags',
                    is_list: true,
                    chip: true
                },
                {
                    text: 'Module khai báo',
                    value: 'modules',
                    is_list: true,
                    child_key: 'name',
                    type: 'link',
                    link: ({ item }) => '/module/detail?name=' + item.name,
                    link_render: ({ item }) => item.name
                },
                {
                    text: 'Code size',
                    value: 'code_size',
                    type: 'number'
                },
                {
                    text: '',
                    value: 'path',
                    type: 'link',
                    link: this.getSwaggerLink,
                    link_render: () => 'Mở bằng swagger',
                    link_target: '_blank',
                    sortable: false
                }
            ]
        };
    },

    methods: {
        getSwaggerLink({ item }) {
            let key = item.method;
            if (item.version) key += '/' + item.version + item.path;
            else key += item.path;
            key = key.replace(/\//g, '_');
            key = key.replace(/\./g, '_');
            key = key.toLocaleLowerCase();

            return (
                '/swagger/#/' +
                (item.tags && item.tags[0] ? item.tags[0] : 'default') +
                '/' +
                key
            );
        }
    }
};
</script>

<style lang="scss" scoped></style>
