<template>
    <v-autocomplete
        v-model="selected_criteries"
        :items="criteries"
        :search-input.sync="search"
        :label="label"
        :hint="hint"
        :no-data-text="noDataText"
        :filter="onFilter"
        item-value="key"
        item-text="name"
        multiple
        clearable
        chips
        hide-selected
        persistent-hint
        return-object
        @input="onInput"
        @change="onChange"
    >
        <template #selection="{ selected, select, item }">
            <v-tooltip bottom color="rgba(0, 0, 0, 1)" :disabled="!item.description">
                <template #activator="{ on }">
                    <v-chip
                        v-on="on"
                        :input-value="selected"
                        close
                        @click="select"
                        @click:close="onRemove(item)"
                    >
                        <span>{{ item.name }}:</span>
                        <b>{{ item.value }}</b>
                    </v-chip>
                </template>
                <span>{{ item.description }}</span>
            </v-tooltip>
        </template>

        <template #item="{item}">
            <v-list-item-content>
                <v-list-item-title>
                    {{ item.name }}
                    <span class="grey--text caption">
                        - {{ '@' + item.key }}
                    </span>
                </v-list-item-title>
                <v-list-item-subtitle>
                    {{ item.description }}
                </v-list-item-subtitle>
            </v-list-item-content>
        </template>
    </v-autocomplete>
</template>

<script>
import Numeral from 'numeral';

export default {
    name: 'search_box',

    props: {
        source_data: {
            type: Array,
            required: true
        },
        criteries: {
            type: Array,
            required: true
        },
        label: {
            type: String,
            default: 'Tìm kiếm'
        },
        noDataText: {
            type: String,
            default: 'Không còn tiêu chí tìm kiếm phù hợp'
        }
    },

    data() {
        return {
            search: null,
            selected_criteries: [],
            filtered_data: this.source_data
        };
    },

    computed: {
        hint() {
            let count = Numeral(this.filtered_data.length).format('0,0');
            return `Có ${count} kết quả phù hợp`;
        }
    },

    methods: {
        onFilter(item, queryText, itemText) {
            let key = queryText.toLocaleLowerCase();
            if (!key.startsWith('@')) return item.default;

            key = key
                .split(':')[0]
                .substr(1)
                .trim();
            return item.key.toLocaleLowerCase().includes(key);
        },

        onRemove(item) {
            let index = this.selected_criteries.findIndex(
                x => x.key === item.key
            );
            if (index < 0) return;
            this.selected_criteries.splice(index, 1);
            this.onChange();
        },

        onInput(items) {
            if (!items || items.length === 0) return;
            let item = items[items.length - 1];

            let keyword = this.search;
            let key;
            let value;
            if (keyword) {
                if (keyword.includes(':')) {
                    key = keyword
                        .split(':')[0]
                        .replace(/\@/g, '')
                        .trim();
                    value = keyword.split(':')[1].trim();
                } else {
                    key = undefined;
                    value = keyword;
                }
            }

            let is_valid = (item.default || item.key === key) && !!value;
            if (!is_valid) {
                this.selected_criteries.pop();
                return;
            }

            item.field = item.field || item.key;
            item.value = value;
            this.search = null;
        },

        onChange() {
            this.filtered_data = this.source_data;

            for (let criteria of this.selected_criteries) {
                if (criteria.filter) {
                    this.filtered_data = this.filtered_data.filter(x =>
                        criteria.filter({ item: x, keyword: criteria.value })
                    );
                } else {
                    this.filtered_data = this.filtered_data.filter(x =>
                        x[criteria.field]
                            .toLocaleLowerCase()
                            .includes(criteria.value)
                    );
                }
            }

            this.$emit('change', this.filtered_data);
        }
    }
};
</script>

<style lang="scss" scoped></style>
