<template>
    <section>
        <span v-if="value === undefined">
            {{ undefinedText }}
        </span>

        <span v-else-if="value === null">
            <v-chip color="grey" dark small>null</v-chip>
        </span>

        <span v-else-if="type === 'boolean'">
            <v-chip v-if="value === true" color="green" dark small>có</v-chip>
            <v-chip v-else color="red" dark small>không</v-chip>
        </span>

        <span v-else-if="type === 'number'">
            {{ value | numeral(numberFormat || '0,0') }}
        </span>

        <template v-else-if="type === 'datetime'">
            <span>
                {{ value | moment('DD-MM-YYYY HH:mm') }}
            </span>
            <span v-if="datetimeDuration">
                ({{ value | moment('from') }})
            </span>
        </template>

        <span v-else-if="type === 'http_method'">
            <v-chip :color="getHttpColor(value)" dark small>
                {{ value.toLocaleLowerCase().trim() }}
            </v-chip>
        </span>

        <template v-else-if="type === 'link'">
            <v-btn
                :to="linkHref ? undefined : link"
                :href="linkHref ? link : undefined"
                :target="linkTarget"
                class="btn_link"
                text
                x-small
            >
                <span class="mr-2">
                    {{ linkRender ? linkRender({ item: value }) : value }}
                </span>
                <v-icon small>mdi-open-in-new</v-icon>
            </v-btn>
        </template>

        <v-chip v-else-if="chip" dark small>
            {{ textRender ? textRender({ item: value }) : value }}
        </v-chip>
        <span v-else>
            {{ textRender ? textRender({ item: value }) : value }}
        </span>
    </section>
</template>

<script>
export default {
    name: 'content_display',

    props: {
        value: {
            required: true
        },
        type: {
            type: String,
            default: 'text'
        },
        chip: {
            type: Boolean,
            default: false
        },
        link: {
            type: String
        },
        linkHref: {
            type: Boolean,
            default: false
        },
        linkRender: {
            type: Function
        },
        undefinedText: {
            type: String,
            default: '-'
        },
        datetimeDuration: {
            type: Boolean,
            default: false
        },
        numberFormat: {
            type: String,
            default: '0,0'
        },
        linkTarget: {
            type: String
        },
        textRender: {
            type: Function
        }
    },

    methods: {
        getHttpColor(method) {
            method = method.toLocaleLowerCase().trim();
            if (method === 'get') return 'blue';
            if (method === 'post') return 'green';
            if (method === 'put') return 'orange';
            if (method === 'delete') return 'red';
            return 'grey';
        }
    }
};
</script>

<style lang="scss" scoped>
::v-deep {
    .btn_link {
        .v-btn__content {
            font-size: 14px !important;
        }
    }
}
</style>
