<template>
    <v-layout v-if="!modu" wrap>
        <v-flex xs12>
            <v-alert type="error">
                <span v-if="!$route.query.name || $route.query.name === ''">
                    Không có module chỉ định.
                </span>
                <span v-else>
                    Không tìm thấy module "{{ $route.query.name }}".
                </span>
            </v-alert>
        </v-flex>
    </v-layout>

    <v-layout v-else text-center wrap>
        <v-flex xs12>
            <h1 class="title font-weight-bold mb-3">Thông tin module</h1>
        </v-flex>

        <v-flex xs12>
            <ContentList :items="contents"></ContentList>
        </v-flex>

        <v-flex xs12>
            <v-card class="mt-3">
                <v-card-title>
                    Danh sách {{ modu.apis.length | numeral('0,0') }} api
                </v-card-title>

                <DataTableApi :apis="modu.list_api"></DataTableApi>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import ContentList from '../components/content_list';
import DataTableApi from '../components/data_table_api';

export default {
    name: 'modu_detail',

    components: {
        ContentList,
        DataTableApi
    },

    data() {
        let modu = this.$store.state.document.modules.find(
            x => x.name === this.$route.query.name
        );
        if (!modu) return {};

        modu.dependency_count = [];
        for (let dependency of modu.module_dependencies) {
            if (modu.dependency_count.includes(dependency.module)) continue;
            modu.dependency_count.push(dependency.module);
        }
        modu.dependency_count = modu.dependency_count.length;

        modu.dependent_count = 0;
        for (let m of this.$store.state.document.modules) {
            if (m.name === modu.name) continue;
            if (m.module_dependencies.some(x => x.module === modu.name)) {
                modu.dependent_count += 1;
                continue;
            }
        }

        modu.list_api = this.$store.state.document.apis.filter(x =>
            modu.apis.includes(x.key)
        );

        return {
            modu: modu,

            contents: [
                {
                    label: 'Tên module',
                    value: modu.name
                },
                {
                    label: 'Mô tả',
                    value: modu.description
                },
                {
                    label: 'Phiên bản',
                    value: modu.version
                },
                {
                    label: 'Người phụ trách',
                    value: modu.author
                },
                {
                    label: 'Số api',
                    value: modu.apis.length,
                    type: 'number'
                },
                {
                    label: 'Số biz',
                    value: modu.bizs.length,
                    type: 'number'
                },
                {
                    label: 'Số model',
                    value: modu.models.length,
                    type: 'number'
                },
                {
                    label: 'Module lệ thuộc',
                    tooltip: 'Số module lệ thuộc do có gọi/sử dụng thành phần',
                    value: modu.dependency_count,
                    type: 'number'
                },
                {
                    label: 'Module tác động',
                    tooltip:
                        'Số module bị lệ thuộc do có gọi/sử dụng thành phần',
                    value: modu.dependent_count,
                    type: 'number'
                },
                {
                    label: 'Code size',
                    value: modu.code_size,
                    type: 'number'
                }
            ],
        };
    }
};
</script>

<style lang="scss" scoped></style>
