<template>
    <v-layout v-if="!api" wrap>
        <v-flex xs12>
            <v-alert type="error">
                <span v-if="!$route.query.key || $route.query.key === ''">
                    Không có api chỉ định.
                </span>
                <span v-else>
                    Không tìm thấy api "{{ $route.query.key }}".
                </span>
            </v-alert>
        </v-flex>
    </v-layout>

    <v-layout v-else text-center wrap>
        <v-flex xs12>
            <h1 class="title font-weight-bold mb-3">Thông tin api</h1>
        </v-flex>

        <v-flex xs12>
            <ContentList :items="contents"></ContentList>
        </v-flex>

        <v-flex xs12>
            <v-card class="mt-3">
                <v-card-title>Sơ đồ code map</v-card-title>

                <Diagram :data="api.code_map"></Diagram>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import ContentList from '../components/content_list';
import Diagram from '../components/diagram';

export default {
    name: 'model_detail',

    components: {
        ContentList,
        Diagram
    },

    data() {
        let api = this.$store.state.document.apis.find(
            x => x.key === this.$route.query.key
        );
        if (!api) return {};

        let api_key = api.method;
        if (api.version) api_key += '/' + api.version + api.path;
        else api_key += api.path;
        api_key = api_key.replace(/\//g, '_');
        api_key = api_key.replace(/\./g, '_');
        api_key = api_key.toLocaleLowerCase();

        api.swagger_link =
            '/swagger/#/' +
            (api.tags && api.tags[0] ? api.tags[0] : 'default') +
            '/' +
            api_key;

        api.modules = this.$store.state.document.modules
            .filter(x => x.apis.includes(api.key))
            .map(x => x.name);

        return {
            api: api,

            contents: [
                {
                    label: 'Swagger',
                    value: 'Mở bằng swagger',
                    type: 'link',
                    link: api.swagger_link,
                    link_target: '_blank'
                },
                {
                    label: 'End-Point',
                    value: api.endpoint
                },
                {
                    label: 'Phương thức',
                    value: api.method,
                    type: 'http_method'
                },
                {
                    label: 'Phiên bản',
                    value: api.version
                },
                {
                    label: 'Mô tả',
                    value: api.summary
                },
                {
                    label: 'Phân loại',
                    value: api.tags,
                    is_list: true,
                    chip: true
                },
                {
                    label: 'Module khai báo',
                    value: api.modules,
                    type: 'link',
                    is_list: true,
                    link: ({ item }) => '/module/detail?name=' + item
                },
                {
                    label: 'Code size',
                    value: api.code_size,
                    type: 'number'
                }
            ]
        };
    }
};
</script>

<style lang="scss" scoped></style>
