<template>
    <div class="diagram-container">
        <svg class="diagram"></svg>
    </div>
</template>

<script>
import DagreD3 from 'dagre-d3/dist/dagre-d3';
import * as D3 from 'd3';

export default {
    name: 'diagram',

    props: {
        data: { type: Object, required: true },
        showEdgeText: {
            type: Boolean,
            default: false
        }
    },

    data: function() {
        return {
            graph_data: null
        };
    },

    mounted: function() {
        this.renderGraph();
    },

    methods: {
        getClusterStyle(cluster) {
            if (cluster.type === 'module') {
                return 'fill: #f5f5f5;';
            }

            if (cluster.type === 'folder') {
                let style = 'stroke-width: 2px; stroke-dasharray: 5, 5;';

                if (cluster.name === 'api') {
                    style += ' fill: #fff2cc;';
                    style += ' stroke: #d6b656;';
                } else if (cluster.name === 'biz') {
                    style += ' fill: #ffe6cc;';
                    style += ' stroke: #d79b00;';
                } else if (cluster.name === 'core') {
                    style += ' fill: #f8cecc;';
                    style += ' stroke: #b85450;';
                }

                return style;
            }
        },

        loadGraphData() {
            let g = new DagreD3.graphlib.Graph({ compound: true })
                .setGraph({})
                .setDefaultEdgeLabel(() => ({}));
            g.graph().rankDir = 'LR';

            // create node
            let nodes = this.data.nodes.filter(
                x => !x.list_child || x.list_child.length === 0
            );
            for (let node of nodes) {
                g.setNode(node.key, {
                    label: node.name,
                    class: 'type-' + node.type,
                    style: node.style
                });
            }

            // create cluster
            let clusters = this.data.nodes.filter(
                x => x.list_child && x.list_child.length > 0
            );
            for (let cluster of clusters) {
                g.setNode(cluster.key, {
                    label: cluster.name,
                    clusterLabelPos: 'top',
                    style: this.getClusterStyle(cluster)
                });

                for (let child_key of cluster.list_child)
                    g.setParent(child_key, cluster.key);
            }

            // set edge
            for (let edge of this.data.edges) {
                g.setEdge(edge.from, edge.to, {
                    curve: D3.curveBasis,
                    label: this.showEdgeText ? edge.text : undefined
                });
            }

            // round all node
            for (let key of g.nodes()) {
                let node = g.node(key);
                node.rx = node.ry = 5;
            }

            this.graph_data = g;
        },

        clearGraph() {
            D3.select('svg')
                .selectAll('*')
                .remove();
        },

        renderGraph() {
            this.clearGraph();
            if (!this.data.nodes || this.data.nodes.length === 0) return;

            let container = D3.select('.diagram-container');
            let svg = D3.select('svg');
            let graph = svg.append('g');
            this.loadGraphData();

            new DagreD3.render()(graph, this.graph_data);
            let graphWidth = this.graph_data.graph().width + 10;
            let graphHeight = this.graph_data.graph().height + 10;
            container.attr('style', `height: ${graphHeight + 40}px`);
            svg.attr('width', graphWidth);
            svg.attr('height', graphHeight);

            let containerWidth = container.node().getBoundingClientRect().width;
            if (graphWidth < containerWidth) svg.classed('center', true);
        }
    },

    watch: {
        data() {
            this.renderGraph();
        }
    }
};
</script>

<style lang="scss">
.diagram-container {
    position: relative;
    overflow: overlay;
    width: 100%;
    padding: 10px;
}

.diagram {
    &.center {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }

    text {
        font-weight: 300;
        font-family: 'Helvetica Neue', Helvetica, Arial, sans-serf;
        font-size: 14px;
    }

    .node {
        rect {
            fill: #fff;
            stroke: #999;
            stroke-width: 1.5px;
        }

        &.type-start rect {
            fill: #dae8fc;
            stroke: #6c8ebf;
            stroke-width: 2px;
        }
    }

    .clusters {
        rect {
            fill: #fff;
            stroke: #999;
            stroke-width: 1.5px;
        }
    }

    .edgePath {
        path {
            stroke: #333;
            stroke-width: 1.5px;
        }
    }
}
</style>
