<template>
    <v-navigation-drawer
        expand-on-hover
        permanent
        height="calc(100vh - 64px)"
        mini-variant-width="10px"
    >
        <template v-slot:prepend>
            <v-list>
                <v-list-item>
                    <v-list-item-title>
                        <v-text-field
                            label="Tìm kiếm"
                            v-model="search"
                        ></v-text-field>
                    </v-list-item-title>
                </v-list-item>
            </v-list>
        </template>

        <v-divider></v-divider>

        <v-list nav dense>
            <v-list-item-group v-model="selected_index" color="primary">
                <v-list-item
                    v-for="(item, index) of filtered_wiki"
                    :key="index"
                    :disabled="index === selected_index"
                    link
                    @click="onClickWikiItem(item)"
                >
                    <v-list-item-content>
                        <v-list-item-title>
                            {{ index + 1 }}.
                            {{ item.title || item.key }}
                        </v-list-item-title>
                        <v-list-item-subtitle
                            v-if="item.author"
                            class="text-right"
                        >
                            {{ item.author }}
                        </v-list-item-subtitle>
                    </v-list-item-content>
                </v-list-item>
            </v-list-item-group>
        </v-list>
    </v-navigation-drawer>
</template>

<script>
export default {
    name: 'wiki_nav_bar',

    props: {
        wikis: {
            type: Array,
            required: true
        }
    },

    data() {
        return {
            search: null,
            selected_index: null
        };
    },

    computed: {
        filtered_wiki() {
            if (!this.search) return this.wikis;

            return this.wikis.filter(x => {
                let search_title = x.title
                    ? x.title.toLocaleLowerCase().includes(this.search)
                    : x.key.toLocaleLowerCase().includes(this.search);

                let search_author = x.author
                    .toLocaleLowerCase()
                    .includes(this.search);

                return search_title || search_author;
            });
        }
    },

    mounted: async function() {
        if (this.$route.query.key)
            await this.selectWikiItem(this.$route.query.key);
    },

    methods: {
        async onClickWikiItem(item) {
            this.$router.push({ query: { key: item.key } });
            await this.selectWikiItem(item.key);
        },

        async selectWikiItem(key) {
            this.selected_index = this.filtered_wiki.findIndex(
                x => x.key === key
            );
            this.$emit(
                'change',
                this.wikis.find(x => x.key === key)
            );
        }
    }
};
</script>

<style lang="scss" scoped></style>
