import Vue from 'vue';
import App from './views/_app.vue';
import Axios from './plugins/axios';
import Moment from './plugins/moment';
import Numeral from './plugins/numeral';
import Vuetify from './plugins/vuetify';
import Router from './router';
import Store from './store';

Axios(Vue);
Moment(Vue);
Numeral(Vue);

new Vue({
    router: Router,
    store: Store,
    vuetify: Vuetify,
    render: function(h) {
        this.axios
            .get('/document.json')
            .then(response => {
                Store.commit('document', response.data);
                Store.commit('appLoading', false);
            })
            .catch(err => {
                Store.commit('document', null);
                Store.commit('appLoading', false);
            });
        return h(App);
    }
}).$mount('#app');
