<template>
    <v-data-table
        :headers="headers"
        :items="items"
        :items-per-page="-1"
        :item-key="expandKey"
        :show-expand="!!expandKey"
        no-data-text="Không có dữ liệu"
        hide-default-footer
        multi-sort
    >
        <template v-for="h in headers" #[`header.${h.value}`]="{ header }">
            <v-tooltip
                :key="h.value"
                :disabled="!header.tooltip"
                color="rgba(0, 0, 0, 1)"
                bottom
            >
                <template #activator="{ on }">
                    <b v-on="on">{{ header.text }}</b>
                </template>
                <span>{{ header.tooltip }} </span>
            </v-tooltip>
        </template>

        <template v-for="h in headers" #[`item.${h.value}`]="{ header, item }">
            <div v-if="!header.is_list" :key="h.value">
                <ContentDisplay
                    :value="item[header.value]"
                    :type="header.type"
                    :chip="header.chip"
                    :link="getLink({ link: header.link, item })"
                    :link-href="header.link_href"
                    :link-target="header.link_target"
                    :link-render="header.link_render"
                    :text-render="header.text_render"
                ></ContentDisplay>
            </div>

            <div v-else :key="h.value">
                <ContentDisplay
                    v-for="child of item[header.value]"
                    :key="h.child_key ? child[h.child_key] : child"
                    :value="child"
                    :type="header.type"
                    :chip="header.chip"
                    :link="getLink({ link: header.link, item: child })"
                    :link-href="header.link_href"
                    :link-target="header.link_target"
                    :link-render="header.link_render"
                    :text-render="header.text_render"
                ></ContentDisplay>
            </div>
        </template>

        <template #expanded-item="{ item, headers }">
            <slot
                name="expanded-item"
                v-bind:item="item"
                v-bind:headers="headers"
            ></slot>
        </template>
    </v-data-table>
</template>

<script>
import ContentDisplay from './content_display';

export default {
    name: 'data_table',

    components: {
        ContentDisplay
    },

    props: {
        headers: {
            type: Array,
            required: true
        },
        items: {
            type: Array,
            required: true
        },
        expandKey: {
            type: String
        },
        undefinedValue: {
            type: String,
            default: '-'
        }
    },

    methods: {
        getLink({ link, item }) {
            if (!link) return;
            if (typeof link === 'string') return link;
            if (typeof link === 'function') return link({ item });
        }
    }
};
</script>

<style lang="scss" scoped></style>
