<template>
    <v-layout text-center wrap>
        <v-flex xs12 v-if="name">
            <h1 class="display-2 font-weight-bold mt-3 mb-3">{{ name }}</h1>
        </v-flex>
        <v-flex xs12 v-if="description">
            <p class="title font-weight-bold mb-3">{{ description }}</p>
        </v-flex>
        <v-flex xs12>
            <ContentList :items="contents"></ContentList>
        </v-flex>
    </v-layout>
</template>

<script>
import ContentList from '../components/content_list';

export default {
    name: 'home',

    components: {
        ContentList
    },

    data() {
        let document = this.$store.state.document;

        let total_code_size = 0;
        for (let module of document.modules) {
            total_code_size += module.code_size;
        }

        let core_framework = (
            document.core_framework.name +
            ' (v' +
            document.core_framework.version +
            ')'
        ).trim();

        return {
            name: document.name,
            description: document.description,

            contents: [
                {
                    label: 'Source code',
                    value: document.source_code_link,
                    type: 'link',
                    link: document.source_code_link,
                    link_target: '_blank',
                    link_href: true,
                },
                {
                    label: 'Lần cập nhật cuối',
                    value: document.update_datetime,
                    type: 'datetime',
                    duration_from_now: true
                },
                {
                    label: 'Kiến trúc sử dụng',
                    value: core_framework
                },
                {
                    label: 'Tổng số module',
                    value: document.modules.length,
                    type: 'number'
                },
                {
                    label: 'Tổng số model',
                    value: document.models.length,
                    type: 'number'
                },
                {
                    label: 'Tổng số biz',
                    value: document.bizs.length,
                    type: 'number'
                },
                {
                    label: 'Tổng số api',
                    value: document.apis.length,
                    type: 'number'
                },
                {
                    label: 'Tổng code size',
                    value: total_code_size,
                    type: 'number'
                }
            ]
        };
    }
};
</script>

<style lang="scss" scoped></style>
