import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        app_loading: true,
        document: {}
    },

    mutations: {
        appLoading(state, value) {
            state.app_loading = value;
        },

        document(state, document) {
            state.document = document || {};

            for (let model of state.document.models) {
                model.modules = state.document.modules
                    .filter(x => x.models.includes(model.name))
                    .map(x => x.name);
            }

            for (let api of state.document.apis) {
                api.modules = state.document.modules.filter(x =>
                    x.apis.includes(api.key)
                );
            }
        }
    }
});
