<template>
    <v-layout v-if="!model" wrap>
        <v-flex xs12>
            <v-alert type="error">
                <span v-if="!$route.query.name || $route.query.name === ''">
                    Không có model chỉ định.
                </span>
                <span v-else>
                    Không tìm thấy model "{{ $route.query.name }}".
                </span>
            </v-alert>
        </v-flex>
    </v-layout>

    <v-layout v-else text-center wrap>
        <v-flex xs12>
            <h1 class="title font-weight-bold mb-3">Thông tin model</h1>
        </v-flex>

        <v-flex xs12>
            <ContentList :items="contents"></ContentList>
        </v-flex>

        <v-flex xs12>
            <v-card class="mt-3">
                <v-card-title>
                    Danh sách {{ model.fields.length | numeral('0,0') }} trường dữ liệu
                </v-card-title>

                <DataTable
                    :headers="field_headers"
                    :items="model.fields"
                ></DataTable>
            </v-card>
        </v-flex>
    </v-layout>
</template>

<script>
import ContentList from '../components/content_list';
import DataTable from '../components/data_table';

export default {
    name: 'model_detail',

    components: {
        ContentList,
        DataTable
    },

    data() {
        let model = this.$store.state.document.models.find(
            x => x.name === this.$route.query.name
        );
        if (!model) return {};

        return {
            model: model,

            contents: [
                {
                    label: 'Tên model',
                    value: model.name
                },
                {
                    label: 'Mô tả',
                    value: model.description
                },
                {
                    label: 'Module khai báo',
                    value: model.modules,
                    type: 'link',
                    is_list: true,
                    link: ({ item }) => '/module/detail?name=' + item
                },
                {
                    label: 'Code size',
                    value: model.code_size,
                    type: 'number'
                }
            ],

            field_headers: [
                {
                    text: 'Tên gọi',
                    value: 'name'
                },
                {
                    text: 'Mô tả',
                    value: 'description'
                },
                {
                    text: 'Kiểu dữ liệu',
                    value: 'type'
                },
                {
                    text: 'Giá trị mặc định',
                    value: 'default'
                },
                {
                    text: 'Tập giá trị cho phép',
                    value: 'enum'
                },
                {
                    text: 'Cho phép null?',
                    value: 'allow_null',
                    type: 'boolean'
                },
                {
                    text: 'Giá trị/độ dài tối thiểu',
                    value: 'min',
                    type: 'number'
                },
                {
                    text: 'Giá trị/độ dài tối đa',
                    value: 'max',
                    type: 'number'
                }
            ]
        };
    }
};
</script>

<style lang="scss" scoped></style>
