<template>
    <v-layout wrap>
        <v-flex xs12>
            <v-card class="mb-5 pa-2">
                <SearchBox
                    :source_data="apis"
                    :criteries="criteries"
                    @change="onChange"
                ></SearchBox>
            </v-card>
        </v-flex>

        <v-flex xs12>
            <v-tabs grow>
                <v-tab href="#tab-list">Danh sách api</v-tab>

                <v-tab-item value="tab-list">
                    <v-card>
                        <DataTableApi :apis="filter_apis"></DataTableApi>
                    </v-card>
                </v-tab-item>
            </v-tabs>
        </v-flex>
    </v-layout>
</template>

<script>
import SearchBox from '../components/search_box';
import DataTableApi from '../components/data_table_api';

export default {
    name: 'api',

    components: {
        SearchBox,
        DataTableApi
    },

    data() {
        return {
            apis: this.$store.state.document.apis,
            filter_apis: this.$store.state.document.apis,

            criteries: [
                {
                    default: true,
                    key: 'path',
                    name: 'End-Point',
                    description: 'Tìm kiếm theo end-point của api'
                },
                {
                    key: 'method',
                    name: 'Phương thức',
                    description: 'Tìm kiếm theo phương thức của api'
                },
                {
                    key: 'version',
                    field: 'version',
                    name: 'Phiên bản',
                    description: 'Tìm kiếm theo phiên bản của api'
                },
                {
                    key: 'desc',
                    field: 'summary',
                    name: 'Mô tả',
                    description: 'Tìm kiếm theo mô tả của api'
                },
                {
                    key: 'tag',
                    name: 'Phân loại',
                    description: 'Tìm kiếm theo phân loại của api',
                    filter: ({ item, keyword }) => {
                        for (let tag of item.tags) {
                            if (tag.toLocaleLowerCase().includes(keyword))
                                return true;
                        }
                        return false;
                    }
                },
                {
                    key: 'module',
                    name: 'Module',
                    description: 'Tìm kiếm theo module khai báo của api',
                    filter: ({ item, keyword }) => {
                        for (let modu of item.modules) {
                            if (modu.name.toLocaleLowerCase().includes(keyword))
                                return true;
                        }
                        return false;
                    }
                }
            ]
        };
    },

    methods: {
        onChange(items) {
            this.filter_apis = items;
        }
    }
};
</script>

<style lang="scss" scoped></style>
